@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Playfair Display";
}

html, body {
    background-color: #F5F5F5;
}

#root {
    background-color: #F5F5F5;
}
#main-container {
    max-width: 1200px;
}

#header-container {
    padding: 3.5rem 1rem 2rem;
}

#header-container h1 {
    margin: auto 0;
    font-size: 40px;
}

#footer-container {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 50px;
    background-color: black;
}

footer p {
    margin: auto 0;
    color: white;
}

@media (max-width: 768px) {
    #header-container h1 {
        font-size: 35px;
    }
}

@media (max-width: 575px) {
    #main-container {
        padding: 0;
        margin: 0;
    }

    #header-container {
        margin-left: 1.5rem;
        padding: 2.5rem 1rem 2rem 0;
    }

    #header-container h1 {
        font-size: 30px;
    }

    footer p {
        font-size: 14px;
    }
}
#results-container {
    margin-bottom: 4rem;
}

#search-wrapper {
    margin: -4.25rem 1rem 3rem auto;
}

#search-wrapper input {
    width: 250px;
    border-radius: 5px 0 0 5px;
}

#search-wrapper input:focus {
    box-shadow: none;
    border: 1px solid lightgray;
}

#search-wrapper button:focus {
    box-shadow: none;
}

#info-box {
    position: absolute;
    left: 45.5%;
    top: 3.5%;
    width: 280px;
    height: 110px;
    border-radius: 0.5rem;
    z-index: 1;
}

#info-box p {
    position: relative;
    margin: 0.75rem;
}

#current-wrapper {
    width: 42.5%;
}

#map-wrapper {
    width: 57.5%;
}

#forecast-wrapper {
    width: 100%;
}

@media (max-width: 1200px) {
    #current-wrapper {
        width: 45%;
    }
    
    #map-wrapper {
        width: 55%;
    }
}

@media (max-width: 992px) {
    #current-wrapper {
        width: 53%;
    }
    
    #map-wrapper {
        width: 47%;
        padding: 0;
        margin: 0;
    }
}

@media (max-width: 768px) {
    #search-wrapper {
        margin: -4.25rem 1rem 3rem auto;
    }

    #search-wrapper input {
        width: 180px;
    }
    
    #current-livemap {
        flex-direction: column;
    }

    #current-wrapper {
        width: 100%;
    }
    
    #map-wrapper {
        width: 100%;
    }
}

@media (max-width: 575px) {
    #search-wrapper {
        margin: -0.5rem 1rem 1rem auto;
    }

    #search-wrapper input {
        width: 200px;
    }
}
#info-btn-box {
    margin: auto 0;
}

.info-button {
    font-size: 30px;
    margin: auto 1rem auto 0;
    cursor: pointer;
}

.modal-text-one {
    margin-bottom: 0.5rem;
}

.modal-text-two {
    margin: 0.5rem 0 0;
}
#current {
    padding-left: 1rem;
    margin: 0 0 0 1.25rem;
}

#current-title {
    width: 100%;
}

#current-title h4 {
    padding: 1rem 0 0;
    margin: 0.5rem 0 1.5rem;
    font-size: 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 450px;
}

#current-content {
    min-width: 330px;
    margin: 0 1rem 1rem 0;
    border: 1px solid black;
    height: 300px;
}

#current-description {
    padding: 0;
    margin: 1.5rem 1rem 0.5rem 1.5rem;
    font-size: 22px;
    font-style: italic;
}

#current-time {
    padding: 0;
    margin: 0 1rem 0 1.5rem;
    font-size: 20px;
}

#current-box {
    margin: 1rem 0;
}

#current-img-temp {
    margin: 0.75rem 0 0.5rem;
}

#current-img-temp img {
    margin: auto 1.5rem;
    width: 90px;
    height: 90px;
}

#current-img-temp h1 {
    max-width: 110px;
    font-size: 40px;
    margin: auto 0.5rem;
}

#current-img-temp p {
    max-width: 150px;
    font-size: 20px;
    margin: auto 1rem;
}

#current-conditions {
    max-width: 100%;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-left: 1.5rem;
}

#humidity-wind, #clouds-rain {
    width: 100%;
}

#humidity-wind-one, #clouds-rain-one {
    width: 42.5%;
}

#humidity-wind-two, #clouds-rain-two {
    width: 57.5%;
}

#current-conditions p {
    margin: auto 0;
    font-size: 18px;
}

.current-icon {
    margin: auto 0.5rem auto 0;
    width: 22px;
    height: 22px;
}

@media (max-width: 1200px) {
    #current {
        margin: 0 0 0 1.25rem;
    }

    #current-content {
        margin: 0 0.5rem 1rem 0;
    }

    #current-description {
        margin: 1.5rem 1rem 1rem 1rem;
        font-size: 22px;
    }

    #current-time {
        margin: 0 1rem 0 1rem;
        font-size: 20px;
    }

    #current-img-temp {
        margin: 1rem 0;
    }

    #current-img-temp img {
        margin: auto 1rem;
        width: 80px;
        height: 80px;
    }

    #current-img-temp h1 {
        max-width: 110px;
        font-size: 35px;
    }

    #current-conditions {
        margin-left: 1rem;
    }

    #humidity-wind-one, #clouds-rain-one {
        width: 45%;
    }
    
    #humidity-wind-two, #clouds-rain-two {
        width: 55%;
    }

    #current-conditions p {
        font-size: 16px;
    }
    
    .current-icon {
        width: 20px;
        height: 20px;
    }
}

@media (max-width: 992px) {
    #current {
        padding-left: 0.5rem;
        margin: 0 0 0 1rem;
    }

    #current-img-temp img {
        margin: auto 0.5rem;
        width: 70px;
        height: 70px;
    }

    #current-img-temp h1 {
        max-width: 110px;
        font-size: 30px;
    }

    #current-img-temp p {
        font-size: 18px;
        margin: auto 1rem;
    }

    #humidity-wind-one, #clouds-rain-one {
        width: 42.5%;
    }
    
    #humidity-wind-two, #clouds-rain-two {
        width: 57.5%;
    }
}

@media (max-width: 768px) {
    #current {
        margin: 0 0 0 1rem;
    }

    #current-content {
        margin: 0 1.5rem 1rem 0;
    }

    #current-description {
        margin: 1.5rem 1rem 1rem 2rem;
        font-size: 24px;
    }

    #current-time {
        margin: 0 1rem 0 2rem;
        font-size: 21px;
    }

    #current-img-temp {
        margin: 0.5rem 0;
    }

    #current-img-temp img {
        margin: auto 2rem;
        width: 85px;
        height: 85px;
    }

    #current-img-temp h1 {
        margin: auto 2rem auto 0;
    }

    #current-img-temp p {
        font-size: 20px;
    }

    #current-conditions {
        margin-left: 2rem;
    }

    #humidity-wind-one, #clouds-rain-one {
        width: 45%;
    }
    
    #humidity-wind-two, #clouds-rain-two {
        width: 55%;
    }

    #current-conditions p {
        font-size: 20px;
    }
    
    .current-icon {
        width: 20px;
        height: 20px;
    }
}

@media (max-width: 575px) {
    #current {
        padding: 0;
    }

    #current-title h4 {
        white-space: normal;
        font-size: 22px;
    }

    #current-content {
        height: auto;
        min-height: 340px;
        margin: 0 1rem 1rem 0;
    }
    
    #current-description {
        margin: 1.5rem 0 1rem 1rem;
        font-size: 20px;
    }

    #current-time {
        margin: 0 0 0 1rem;
        font-size: 18px;
    }

    #current-img-temp img {
        margin: auto 0 auto 0.5rem;
        width: 70px;
        height: 70px;
    }

    #current-img-temp h1 {
        margin: auto 0.5rem;
        font-size: 28px;
    }

    #current-img-temp p {
        font-size: 18px;
        margin: auto 0 auto 1rem;
    }

    #humidity-wind, #clouds-rain {
        flex-direction: column;
    }

    #humidity-wind-one, #humidity-wind-two, #clouds-rain-one, #clouds-rain-two {
        width: 100%;
    }

    #current-conditions {
        grid-gap: 0rem;
        gap: 0rem;
        margin-left: 1rem;
    }

    #current-conditions p {
        font-size: 18px;
        margin: auto 0;
        padding-bottom: 0.5rem;
    }
    
    .current-icon {
        width: 22px;
        height: 22px;
    }
}
#error-container {
    padding-left: 1rem;
    margin: 0 0.75rem 0 1.25rem;
}

#error-title {
    width: 100%;
}

#error-title h4 {
    padding: 1rem 0 0;
    margin: 0.5rem 0 1.5rem;
    font-size: 24px;
}

#error-container p {
    font-size: 20px;
}
#map {
    padding-left: 1rem;
    margin: 0 2.25rem 0 1.25rem;
}

#map-title {
    width: 100%;
}

#map-title h4 {
    padding: 1rem 0 0;
    margin: 0.5rem 0 1.5rem;
    font-size: 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 600px;
}

.leaflet-container {
    width: 100%;
    height: 300px;
    z-index: 0;
    border: 1px solid black;
}

@media (max-width: 1200px) {
    #map {
        padding-left: 0;
        margin: 0 2.25rem 0 0.5rem;
    }
}

@media (max-width: 992px) {
    #map {
        margin: 0 1.5rem 0 0.5rem;
    }
}

@media (max-width: 768px) {
    #map {
        padding-left: 0.5rem;
        margin: 0 1.5rem 0 1rem;
    }
}

@media (max-width: 575px) {
    #map-title h4 {
        white-space: normal;
        font-size: 22px;
    }

    #map {
        margin: 0 1rem 0 1rem;
        padding: 0;
    }
}
#forecast {
    padding-left: 1rem;
    margin: 1rem 1.25rem;
}

#forecast-title h4 {
    padding: 1rem 0 0;
    margin: 0.5rem 0 1.5rem;
    font-size: 24px;
}

#faExclamation-icon {
    padding: 1.125rem 0 0;
    margin: 0.5rem 0 1.5rem 1rem;
    font-size: 45px;
    cursor: pointer;
}

#daily-wrapper {
    display: flex;
    flex-direction: column;
}

@media (max-width: 992px) {
    #forecast {
        padding-left: 0.5rem;
        margin: 1rem 1rem;
    }

    #daily-wrapper {
        display: grid;
        grid-gap: 0.5rem;
        gap: 0.5rem;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 575px) {
    #forecast {
        padding: 0;
        margin-left: 1rem;
    }

    #forecast-title h4 {
        font-size: 22px;
        padding: 1rem 0 0;
        margin: 0.5rem 0 1.5rem;
        width: 95%;
    }

    #faExclamation-icon {
        font-size: 40px;
    }
}
#daily {
    display: flex;
    margin: 0 1rem 1rem 0;
    border: 1px solid black;
}

#daily-date {
    margin: auto 0.5rem auto 1rem;
    width: 120px;
    font-size: 20px;
    text-align: center;
}

#img-description-box {
    display: flex;
    width: 100%;
    max-width: 430px;
}

#daily img {
    width: 70px;
    height: 70px;
    margin: auto 1.5rem;
}

#daily-description {
    margin: auto 1rem;
}

#daily-description p {
    margin: auto 0;
    font-size: 20px;
    font-style: italic;
    text-align: start;
}

#temp-box, #rain-wind-box, #sun-box {
    margin: auto 0;
    margin-left: 12px;
}

#temp-box {
    width: 130px;
    flex-direction: column;
}

#rain-wind-box {
    width: 180px;
    flex-direction: column;
}

#sun-box {
    width: 170px;
    flex-direction: column;
}

.daily-icon {
    margin: auto 0.5rem;
    font-size: 20px;
}

#temp-box p, #rain-wind-box p, #sun-box p {
    margin: auto 0;
    text-align: start;
    font-size: 18px;
}

@media (max-width: 1200px) {
    #daily-date {
        margin: auto 0 auto 1rem;
        font-size: 19px;
        text-align: left;
    }

    #img-description-box {
        max-width: 400px;
    }
    
    #daily img {
        width: 65px;
        height: 65px;
        margin: auto 1rem;
    }
    
    #daily-description {
        margin: auto 0.5rem;
    }
    
    #daily-description p {
        font-size: 19px;
    }
}

@media (max-width: 1150px) {
    #img-description-box {
        max-width: 360px;
    }
}

@media (max-width: 1100px) {
    #img-description-box {
        max-width: 320px;
    }
}

@media (max-width: 1050px) {
    #img-description-box {
        max-width: 290px;
    }
}

@media (max-width: 992px) {
    #daily {
        flex-direction: column;
        margin: 0 0.5rem 0.5rem 0;
    }

    #daily-date {
        margin: 1rem 0 auto 1rem;
    }

    #img-description-box {
        max-width: 430px;
    }
    
    #daily img {
        width: 70px;
        height: 70px;
    }
    
    #daily-description p {
        font-size: 20px;
    }

    #temp-box, #rain-wind-box, #sun-box {
        flex-direction: row;
        width: 100%;
        margin-bottom: 0.25rem;
    }

    #sun-box {
        margin-bottom: 1rem;
    }

    #max-temp, #rain, #sunrise {
        width: 45%;
    }

    #min-temp, #wind, #sunset {
        width: 55%;
    }
}

@media (max-width: 768px) {
    #daily-date {
        font-size: 17px;
    }

    #daily img {
        width: 60px;
        height: 60px;
        margin: auto 0 auto 0.5rem;
    }

    #daily-description p {
        font-size: 18px;
    }

    #max-temp, #rain, #sunrise {
        width: 40%;
    }

    #min-temp, #wind, #sunset {
        width: 60%;
    }

    .daily-icon {
        margin: 0.25rem 0.5rem;
        font-size: 22px;
    }
}

@media (max-width: 575px) {
    #daily {
        margin: 0;
    }

    #daily-date {
        font-size: 16px;
        margin: 1rem 0 0 0.5rem;
    }

    #img-description-box {
        flex-direction: column;
        margin-bottom: 1rem;
    }

    #daily img {
        margin: 0 auto;
    }

    #daily-description {
        margin: 0 0 0 0.5rem;
    }

    #temp-box, #rain-wind-box, #sun-box {
        margin: 0;
    }

    #sun-box {
        margin-bottom: 0.5rem;
    }

    #max-temp, #rain, #sunrise {
        width: 45%;
    }

    #min-temp, #wind, #sunset {
        width: 55%;
    }

    #max-temp p, #rain p, #sunrise p, #min-temp p, #wind p, #sunset p {
        font-size: 15px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .daily-icon {
        margin: 0.25rem 0.25rem 0.25rem 0.5rem;
        font-size: 18px;
    }
}
#alerts-btn-box {
    margin: auto 0 auto 1rem;
}

.alerts-button {
    font-size: 30px;
    margin: auto 1rem auto 0;
    cursor: pointer;
}

#alerts-message div:not(:last-child) {
    border-bottom: 1px solid lightgrey;
}

#alerts-message div p {
    margin-bottom: 0.5rem;
}

#alerts-message div p:first-child {
    margin-top: 0.5rem;
}

#alerts-message div p:last-child {
    margin-bottom: 0.75rem;
}
